eAAA;EACA,YAAA;EACA,iBAAA;EACA,kBAAA;EACA,cAAA;EACA,cAAA;AFhCN;AEkCM;EACE,qBAAA;EACA,cAAA;AFhCR;AEoCI;EACE,oBAAA;EACA,mBAAA;AFlCN;AEuCA;EACE,kBAAA;EACA,YAAA;EACA,mBAAA;AFrCF;AEkCA;EAMI,mBAAA;EACA,WAAA;EACA,kBAAA;EACA,YAAA;EACA,iBAAA;AFrCJ;AE2BA;EAaM,WAAA;EACA,kBAAA;EACA,eAAA;EACA,iBAAA;EACA,kBAAA;AFrCN;AEoBA;EAsBI,kBAAA;EACA,OAAA;EACA,MAAA;EACA,WAAA;EACA,YAAA;EACA,UAAA;EACA,YAAA;AFvCJ;AEWA;EAgCI,WAAA;EACA,yBAAA;EACA,uBAAA;AFxCJ;AEMA;EAqCM,WAAA;EACA,uBAAA;EACA,sBAAA;EACA,kBAAA;EACA,YAAA;EACA,eAAA;AFxCN;AE6CA;EACE,eAAA;EACA,YAAA;EACA,WAAA;EACA,iCAAA;EACA,yBAAA;EACA,cAAA;EACA,aAAA;AF3CF;AE6CE;EACE,YAAA;AF3CJ;AE8CE;EACE,WAAA;EACA,kBAAA;EACA,kBAAA;EACA,cAAA;EACA,kBAAA;EACA,gCAAA;EACA,wBAAA;EACA,UAAA;AF5CJ;AE8CI;EACE,mBAAA;AF5CN;AE+CI;EACE,mBAAA;AF7CN;AEgDI;EACE,mBAAA;AF9CN","file":"spiral.less","sourcesContent":["//\n// Mixins\n// --------------------------------------------------\n\n// Clearfix\n// --------\n// For clearing floats like a boss h5bp.com/q\n.clearfix, .cf {\n  *zoom: 1;\n\n  &:before,\n  &:after {\n    display: table;\n    content: \"\";\n    // Fixes Opera/contenteditable bug:\n    // http://nicolasgallagher.com/micro-clearfix-hack/#comment-36952\n    line-height: 0;\n  }\n\n  &:after {\n    clear: both;\n  }\n}\n\n// Center-align a block level element\n// ----------------------------------\n.center-block() {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n// Sizing shortcuts\n// -------------------------\n.size(@width, @height) {\n  width: @width;\n  height: @height;\n}\n\n.square(@size) {\n  .size(@size, @size);\n}\n\n// Placeholder text\n// -------------------------\n.placeholder(@color: @placeholderText) {\n  &:-moz-placeholder {\n    color: @color;\n  }\n\n  &:-ms-input-placeholder {\n    color: @color;\n  }\n\n  &::-webkit-input-placeholder {\n    color: @color;\n  }\n}\n\n// CSS3 PROPERTIES\n// --------------------------------------------------\n\n// Border Radius\n.border-radius(@radius) {\n  -webkit-border-radius: @radius;\n  -moz-border-radius: @radius;\n  border-radius: @radius;\n}\n\n// Single Corner Border Radius\n.border-top-left-radius(@radius) {\n  -webkit-border-top-left-radius: @radius;\n  -moz-border-radius-topleft: @radius;\n  border-top-left-radius: @radius;\n}\n\n.border-top-right-radius(@radius) {\n  -webkit-border-top-right-radius: @radius;\n  -moz-border-radius-topright: @radius;\n  border-top-right-radius: @radius;\n}\n\n.border-bottom-right-radius(@radius) {\n  -webkit-border-bottom-right-radius: @radius;\n  -moz-border-radius-bottomright: @radius;\n  border-bottom-right-radius: @radius;\n}\n\n.border-bottom-left-radius(@radius) {\n  -webkit-border-bottom-left-radius: @radius;\n  -moz-border-radius-bottomleft: @radius;\n  border-bottom-left-radius: @radius;\n}\n\n// Single Side Border Radius\n.border-top-radius(@radius) {\n  .border-top-right-radius(@radius);\n  .border-top-left-radius(@radius);\n}\n\n.border-right-radius(@radius) {\n  .border-top-right-radius(@radius);\n  .border-bottom-right-radius(@radius);\n}\n\n.border-bottom-radius(@radius) {\n  .border-bottom-right-radius(@radius);\n  .border-bottom-left-radius(@radius);\n}\n\n.border-left-radius(@radius) {\n  .border-top-left-radius(@radius);\n  .border-bottom-left-radius(@radius);\n}\n\n// Drop shadows\n.box-shadow(@shadow) {\n  -webkit-box-shadow: @shadow;\n  -moz-box-shadow: @shadow;\n  box-shadow: @shadow;\n}\n\n// Drop shadows\n.box-shadow(@shadow, @shadow2) {\n  -webkit-box-shadow: @shadow, @shadow2;\n  -moz-box-shadow: @shadow, @shadow2;\n  box-shadow: @shadow, @shadow2;\n}\n\n// Drop shadows\n.box-shadow(@shadow, @shadow2, @shadow3) {\n  -webkit-box-shadow: @shadow, @shadow2, @shadow3;\n  -moz-box-shadow: @shadow, @shadow2, @shadow3;\n  box-shadow: @shadow, @shadow2, @shadow3;\n}\n\n// Transitions\n.transition(@transition) {\n  -webkit-transition: @transition;\n  -moz-transition: @transition;\n  -o-transition: @transition;\n  transition: @transition;\n}\n\n.transition-delay(@transition-delay) {\n  -web